import { useState } from "react";

export default function TextForm(props) {
  const handleUpCase = () => {
    setText(text.toUpperCase());
    props.showAlert("success","Converted to Upper Case");
  };

  const handleLowerCase = () => {
    setText(text.toLowerCase());
    props.showAlert("success","Converted to Lower Case");
  };
  const handleOnChange = (event) => {
    setText(event.target.value);
  };

  const handleClear = () => {
    setText("");
    props.showAlert("success","Text Cleared");
  };

  const handleOnChangeF = (event) => {
    setFindText(event.target.value);
  };

  const handleOnChangeR = (event) => {
    setReplaceText(event.target.value);
  };

  const handleFandR = () => {
    let newtext = text.replace(findtext, replacetext);
    setText(newtext);
    setFindText("");
    setReplaceText("");
  };

  const handleCopy=()=>{
    navigator.clipboard.writeText(text);
    props.showAlert("success","Copied to clipboard");
  }

  const wordCount=(str)=>{
    var wordArray= str.split(" ");
    let count=0;
    for(let i=0;i<wordArray.length;i++){
      if(wordArray[i]!==""){
        count=count+1;
      }
    }
    return count;
  }
  
  // const handleCapitalize=()=>{
  //   let myArray=text.split(".");
  //   for(let i=0;i<myArray.length;i++){
  //     for(let j=0;j<myArray[i].charAt(i).length;j++){
  //       if(myArray[i].charAt(j)!==" "){
  //         myArray[i].charAt(j).replace(myArray[i].charAt(j),myArray[i].charAt(j).toUpperCase())
  //         break;
  //       }
  //     }
  //   }
  //   setText(myArray.join("."));
  // }
  const [text, setText] = useState("");
  const [findtext, setFindText] = useState("");
  const [replacetext, setReplaceText] = useState("");
  return (
    <>
      <div className="container " >
        <h1 className="p-2" >{props.heading}</h1>
        <div className="mb-3 my-3">
          <textarea
            className="form-control"
            id="exampleFormControlTextarea1"
            value={text}
            rows="8"
            onChange={handleOnChange}
          ></textarea>
          <button className="btn btn-primary my-3 mx-2" onClick={handleUpCase}>
            Convert to Upper Case
          </button>
          <button
            className="btn btn-primary my-3 mx-2"
            onClick={handleLowerCase}
          >
            Convert to Lower Case
          </button>
          <button className="btn btn-primary my-3 mx-2" onClick={handleClear}>
            Clear
          </button>

          <button className="btn btn-primary my-3 mx-2" onClick={handleCopy}>
            Copy
          </button>
          {/* <button className="btn btn-primary my-3 mx-2" onClick={handleCapitalize}>
            Capitalize First Letter
          </button> */}

          
        </div>
      </div>
      <div className="container my-1 ">
        <label htmlFor="Find" className="mx-1 d-block my-1 h4">
          Find
        </label>
        <input
          type="text"
          className="p-1 mx-1 "
          value={findtext}
          onChange={handleOnChangeF}
        />

        <label htmlFor="Replace" className="mx-1 my-1 d-block h4">
          Replace
        </label>
        <input
          type="text"
          className="p-1 mx-1"
          value={replacetext}
          onChange={handleOnChangeR}
        />
        <button
          className="btn btn-primary my-3 mx-2 d-block"
          onClick={handleFandR}
        >
          Find and Replace
        </button>
      </div>
      <div className="container">
        <h2>Text Summary</h2>
        {wordCount(text)} words and {text.length} characters
      </div>
    </>
  );
}
